import {
  BooleanValue,
  StringValue,
  SchemaDefinition,
  NumberValue,
  RecordValue,
} from '@coveo/bueno';
import {SearchParameters} from './search-parameter-actions';

export const searchParametersDefinition: SchemaDefinition<
  Required<SearchParameters>
> = {
  q: new StringValue(),
  enableQuerySyntax: new BooleanValue(),
  aq: new StringValue(),
  cq: new StringValue(),
  firstResult: new NumberValue({min: 0}),
  numberOfResults: new NumberValue({min: 0}),
  sortCriteria: new StringValue(),
  f: new RecordValue(),
  fExcluded: new RecordValue(),
  cf: new RecordValue(),
  nf: new RecordValue(),
  df: new RecordValue(),
  debug: new BooleanValue(),
  sf: new RecordValue(),
  tab: new StringValue(),
  af: new RecordValue(),
};
